import React, { Component } from 'react';
import pageStyle from '../page.scss';
import Search from '../../component/common/Search';
import Header from './component/Header';
require('./ServiceList.css');
class ServiceList extends Component {
    constructor(...args) {
        super(...args);
    }
    componentDidMount() {
        document.title = '-';
    }
    render() {
        let aText = [
            { "address": "", "content": "[]", "date": "2018-5-24", "price": "230" },
            { "address": "2", "content": "[]2", "date": "2018-5-25", "price": "250" },
            { "address": "3", "content": "[]3", "date": "2018-5-26", "price": "280" }
        ];
        return (
            <div className={pageStyle['page']} id="serviceList">
                <React.Fragment>
                    <div className={'box'}>
                        <Header title=""/>
                        <div className={'menu'}>
                            <div className={'wrap'}>
                                <span></span>
                                <div className={'form'}>
                                    <Search title=""/>
                                </div>
                                <input type="button" value="" className={'btn'} />
                            </div>
                        </div>
                        <div className={'box_list'}>
                            {
                                aText.map((text, index) => {
                                    return (
                                        <div key={index} className={'list_item'}>
                                            <ul className={'left'}>
                                                <li className={'item1'}>{text.address}</li>
                                                <li className={'item2'}>{text.content}</li>
                                                <li className={'item3'}>{text.date}</li>
                                            </ul>
                                            <div className={'right'}>{text.price}</div>
                                        </div>
                                    )
                                })
                            }

                        </div>
                    </div>
                </React.Fragment>
            </div>
        );
    }
}
export default ServiceList;
