import React, { Component } from 'react';
import Header from './component/Header';
import pageStyle from '../page.scss';
import './SendKey.css';
class SendKey extends Component {
    constructor (...args) {
        super (...args);
        this.state = {
            foreveryKey: false,        //
            startTime: '',
            endTime: '',
            user: ''
        };
    }
    componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }
    fnLimitKey = () => {
        this.setState({ foreveryKey: false });
    }
    fnForeveryKey = () => {
        this.setState({ foreveryKey: true });
    }
    fnStartTime = (ev) => {
        this.setState({ startTime: ev.target.value });
    }
    fnEndTime = (ev) => {
        this.setState({ endTime: ev.target.value });
    }
    fnUser = (ev) => {
        this.setState({ user: ev.target.value });
    }
    fnSend = () => {
        let key = this.state.foreveryKey ? "" : "";
        console.log(`${key},:${this.state.startTime},:${this.state.endTime},${this.state.user}`);
    }
    render() {
        return (
            <div className={pageStyle['page']}>
                <React.Fragment>
                    <div className={"box"} id="sendkey">
                        <Header title="" />
                        <ul>
                            <li className="type">
                                
                                <p>
                                    <a href="javascript:;" className={this.state.foreveryKey ? "" : "active"} onClick={ this.fnLimitKey } ></a>
                                    <a href="javascript:;" className={this.state.foreveryKey ? "active" : ""} onClick={ this.fnForeveryKey }></a>
                                </p>
                            </li>
                            <li className="time">
                                
                                <p>
                                    <input type="text" onChange={this.fnStartTime} />
                                    <span></span>
                                    <input type="text" onChange={this.fnEndTime}/>
                                </p>
                            </li>
                            <li className="user">
                                
                                <p>
                                    <input type="text" placeholder="" onChange={this.fnUser} />
                                </p>
                            </li>
                            <a href="javascript:;" className="send" onClick={ this.fnSend } ></a>
                        </ul>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}

export default SendKey
