import React, { Component } from 'react';
import Header from './component/Header';
import pageStyle from '../page.scss';
import './IntelligentUser.css';
class IntelligentUser extends Component {
    constructor (...args) {
        super (...args);
        this.state = {
            freeze: "none"
        };
    }
    componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }
    fnFreeze = () => {
        this.setState({ freeze: "block" });
    }
    fnCancel = () => {
        this.setState({ freeze: "none" });
    }
    fnEnsure = (aa) => {
        this.setState({ freeze: "none" });
        console.log("");
    }
    render() {
        let aText = {
            user: "M123456789",
            type: "",
            static: "",
            time: "2018-5-232019-5-23"
        };
        return (
            <div className={pageStyle['page']} id="intelligentuser">
                <React.Fragment>
                    <div className={"box"} >
                        <Header title="" />
                        <ul>
                            <li>{aText.user}</li>
                            <li>{aText.type}</li>
                            <li>{aText.static}</li>
                            <li>{aText.time}</li>
                            <a href="javascript:;" className="freeze" onClick={this.fnFreeze}></a>
                            <a href="javascript:;" className="del"></a>
                        </ul>
                    </div>
                    <div className="wrap" style={{ display: this.state.freeze }}>
                        <div className="content">
                            <p></p>
                            <a href="javascript:;" className="ensure" onClick={this.fnEnsure}></a>
                            <a href="javascript:;" className="cancel" onClick={this.fnCancel} ></a>
                        </div>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}

export default IntelligentUser
