import React, { Component } from 'react';
import Header from './component/Header';
import Electricity from './component/Electricity';
import pageStyle from '../page.scss';
import './IntelligentLock.css';
class IntelligentLock extends Component {
    constructor (...args) {
        super (...args);
    }
    componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }
    render() {
        let listData = [
            { id: "M456789", account: "NBG789", user: "", electricity: 0 },
            { id: "M456790", account: "NBG789", user: "2", electricity: 20 },
            { id: "M456710", account: "NBG789", user: "3", electricity: 40 },
            { id: "M456722", account: "NBG789", user: "4", electricity: 60 },
            { id: "M456733", account: "NBG789", user: "5", electricity: 100 }
        ];
        let ulComp = (
            listData.map( item => {
                return (
                    <ul key={item.id}>
                        <li>ID:{item.id}</li>
                        <li>:{item.account}</li>
                        <li>:{item.user}</li>
                        <Electricity num={item.electricity} className="aaa" />
                    </ul>
                )
            })
        )
        return (
            <div className={pageStyle['page']} id="intelligentlock">
                <React.Fragment>
                    <div className={"box"} id="intelligentlock">
                        <Header title="" />
                        <div className="menu">
                            <input type="text" placeholder="/" />
                            <a href="#"></a>
                        </div>
                        <div className="list">
                            { ulComp }
                        </div>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}

export default IntelligentLock
