import React, { Component } from 'react';
import pageStyle from '../page.scss';
import ImagePicker from '../../component/common/ImagePicker';
import Header from './component/Header';
require("./ServiceWrite.css")

class ServiceWrite extends Component {
    constructor( ...args ) {
        super( ...args );
    }

    componentDidMount() {
        document.title = '';
    }

    render() {
        let selectJson = [
            { name: ':', store: [ '', '', '', '', '' ] },
            { name: ':', store: [ '', '', '', '' ] },
            { name: ':', store: [ '', '', '', '', '' ] },
            { name: ':', store: [ '', 'VIP' ] }
        ];

        return (
            <div className={pageStyle['page']} id="servicewrite" >
                <React.Fragment>
                    <div className={'box'}>
                        <Header title=""/>
                        <ul className={'list'}>
                            {
                                selectJson.map( (listItem, index) => {
                                    return (
                                        <li key={index}>{ listItem.name }
                                            <select>
                                                <option >{ listItem.name }</option>
                                                {
                                                    listItem.store.map( (item, num) => {
                                                        return (
                                                            <option key={num}>{item}</option>
                                                        );
                                                    })
                                                }
                                            </select>
                                        </li>
                                    );
                                })
                            }
                            <li>:
                                <textarea rows="3" cols="20" placeholder="">
                                </textarea>
                            </li>
                            <li>
                                <ImagePicker/>
                            </li>
                            <a href="javascript:;" className={'reset'}></a>
                            <a href="javascript:;" className={'prev'}></a>
                        </ul>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}
export default ServiceWrite;
