import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Header.css'
class Header extends Component {
    constructor (...args) {
        super (...args);
    }
    static propTypes={
        title: PropTypes.string
    }
    fnReturn = () => {
        console.log("");
    }
    render () {
        return (
            <div id={'top'}>
                <a href="javascript:;" className="return" onClick={this.fnReturn}></a>
                { this.props.title }
            </div>
        );
    }
}
export default Header
