import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './WithDraw.css';
class WithDraw extends Component {
    constructor (...args) {
        super (...args);
        this.state = {
            radioValue: '',                       //
            price: 0                       //
        };
    }
    static propTypes = {
        type: PropTypes.string,
        aaa: PropTypes.func
    }
    handleChange = (e) => {            //
        this.setState({
            radioValue: e.target.value
        });
    }
    fnWithCash = (ev) => {
        this.setState({ price: ev.target.value });
    }
    fnEnsure = () => {
        if (this.state.radioValue === "") {
            console.log("");
        } else if (this.state.price === 0) {
            console.log("");
        } else {
            console.log(`${this.state.radioValue},${this.state.price}`);
        }
    }
    render () {
        let bankText = [
            { name: "", carid: "61285479654478923678", value: "gongsi", idname: "company" },
            { name: "", carid: "61285479654478923678", value: "faren", idname: "corporation" }
        ];
        let liComp = (
            bankText.map( (item, index) => {
                return (
                    <li key={index}>
                        <label htmlFor={item.idname} className={ this.state.radioValue === item.value ? "active" : "" }></label>
                        <input
                            type="radio"
                            name="account"
                            value={item.value}
                            id={item.idname}
                            onChange={this.handleChange}
                        />
                        {item.name}[{item.carid}]
                    </li>
                )
            } )
        );
        return (
            <div id="withDraw" style={{ display: this.props.type }}>
                <div className="box">
                    <h3></h3>
                    <p></p>
                    <ul>
                        {liComp}
                    </ul>
                    <p></p>
                    <div className="text" onChange={ this.fnWithCash }>
                        <input type="text"/>
                    </div>
                    <a href="javascript:;" className="reset" onClick={this.props.aaa}></a>
                    <a href="javascript:;" className="ensure" onClick={this.fnEnsure} ></a>
                </div>
            </div>

        );
    }
}
export default WithDraw
