import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './tag.scss';
class Tag extends Component {
    static propTypes={
        text: PropTypes.string
    }
    render() {
        return (
            <div className={style['tag']}><span>{this.props.text}</span></div>
        )
    }
}
export default Tag;