import React, { Component } from 'react'
import pageStyle from '../page.scss';

require('./UnionStore.css');

class UnionStore extends Component {
    constructor(...args) {
        super(...args);
    }
    componentDidMount() {
        document.title = '';
    }
    render() {
        let sBannnerImg = '/static/union_store/banner.png';
        let aListImg = [
            { src: '/static/union_store/fangyuanguanli.png', text: '' },
            { src: '/static/union_store/hetongguanli.png', text: '' },
            { src: '/static/union_store/zhinengsuo.png', text: '' },
            { src: '/static/union_store/caiwu.png', text: '' },
            { src: '/static/union_store/renyuan.png', text: '' },
            { src: '/static/union_store/weixiu.png', text: '' },
            { src: '/static/union_store/kaquanbao.png', text: '' },
            { src: '/static/union_store/zufangbaoxian.png', text: '' },
            { src: '/static/union_store/xiazaiapp.png', text: 'APP' }
        ];
        return (
            <div className={pageStyle['page']} id="banner">
                <React.Fragment>
                    <div className="box">
                        <div className={'banner'} >
                            <img src={sBannnerImg} />
                        </div>
                        <div className={'list'}>
                            {
                                aListImg.map((list, index) => {
                                    return (
                                        <a href="#unionStore" key={index} className={'item'}>
                                            <img src={list.src} />
                                            <p>{list.text}</p>
                                        </a>
                                    )
                                })
                            }

                        </div>
                    </div>
                </React.Fragment>
            </div>
        );
    }
}
export default UnionStore
