import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './houseInfomation.scss';
import pageStyle from '../page.scss';
import { Carousel, WingBlank } from 'antd-mobile';

const res = [
    "static/test/fangyuantupian",
    "static/test/fangyuantupian",
    "static/test/fangyuantupian"
]
class HouseInfomation extends Component {
    state = {
        data: res
    }

    async componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }

    render() {
        console.log("houseInfomation");
        return (
            <div className={pageStyle['page']}>
                <React.Fragment>
                    <div className={style['content']}>
                        <div className={style['house_main']}>
                            <div className={style['broadcast']}>
                                <Carousel
                                    autoplay={true}
                                    infinite
                                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                                    afterChange={index => console.log('slide to', index)}
                                >
                                    {this.state.data.map(val => (
                                        <a
                                            key={val}
                                            href=""
                                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                                        >
                                            <img
                                                src={`/${val}.png`}
                                                alt=""
                                                style={{ width: '100%', verticalAlign: 'top' }}
                                                onLoad={() => {
                                                
                                                    window.dispatchEvent(new Event('resize'));
                                                    this.setState({ imgHeight: 'auto' });
                                                }}
                                            />
                                        </a>
                                    ))}
                                </Carousel>
                            </div>
                            <div className={style['house_title_price']}>
                                <span>11</span>
                            </div>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div className={style['des']}><span></span></div>
                        </div>
                        <div className={style['authentication']}>
                            <div>   
                                <div><img src="" /></div>
                                <div><span></span></div>
                            </div>
                        </div>
                        <div className={style['house_infomation']}>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div className={style['info']}>
                                <div className={style['info_item']}>
                                    <div className={style['info_left']}><span className={style['info_title']}> : </span><span className={style['info_value']}>HN3301001443</span></div>
                                    <div className={style['info_right']}><span className={style['info_title']}> : </span><span className={style['info_value']}>6/18</span></div>
                                </div>
                                <div className={style['info_item']}>
                                    <div className={style['info_left']}><span className={style['info_title']}> : </span><span className={style['info_value']}>101</span></div>
                                    <div className={style['info_right']}><span className={style['info_title']}> : </span><span className={style['info_value']}>-</span></div>
                                </div>
                                <div className={style['info_item']}>
                                    <div className={style['info_left']}><span className={style['info_title']}> : </span><span className={style['info_value']}></span></div>
                                    <div className={style['info_right']}><span className={style['info_title']}> : </span><span className={style['info_value']}>15</span></div>
                                </div>
                                <div className={style['info_item']}><span className={style['info_title']}> : </span><span className={style['info_value']}></span></div>
                            </div>
                        </div>
                        <div className={style['house_configure']}>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div>

                            </div>
                        </div>
                        <div className={style['house_position']}>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div>
                                <img src="/static/test/ditu.png" />
                            </div>
                        </div>
                        <div className={style['price_infomation']}>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div>
                                <img src="/static/test/zoushi.png" />
                            </div>
                        </div>
                        <div className={style['nearby_source']}>
                            <div className={style['tagTitle']}>
                                <div className={style['sign']}></div><span></span>
                            </div>
                            <div>
                                <Carousel
                                    autoplay={true}
                                    infinite
                                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                                    afterChange={index => console.log('slide to', index)}
                                >
                                    {this.state.source.map(this.nearbySource)}
                                </Carousel>
                            </div>
                        </div>
                    </div>
                    <div className={style['botton_bar']}>
                        <div>

                        </div>
                        <div>

                        </div>
                        <div className={style['apply']}>

                        </div>
                    </div>
                </React.Fragment>
            </div>
        );
    }
}
export default HouseInfomation;