import React, { Component } from 'react';
import Header from './component/Header';
import Electricity from './component/Electricity';
import pageStyle from '../page.scss';
import './IntelligentInfo.css';
class IntelligentInfo extends Component {
    constructor (...args) {
        super (...args);
    }
    componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }
    render() {
        let userInfo = {
            id: "M123456",
            account: "NBG789",
            user: "",
            electricity: 20
        };
        return (
            <div className={pageStyle['page']}>
                <React.Fragment>
                    <div className={"box"} id="intelligentinfo">
                        <Header title="" />
                        <ul>
                            <li>ID{userInfo.id}</li>
                            <li>{userInfo.account}</li>
                            <li>{userInfo.user}</li>
                            <li><a href="javascript:;" className="cancel" ></a></li>
                            <a href="javascript:;" className="send" ></a>
                            <a href="javascript:;" className="management" ></a>
                            <a href="javascript:;" className="record" ></a>
                            <Electricity num={userInfo.electricity} />
                        </ul>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}

export default IntelligentInfo
