import React, { Component } from 'react';
import pageStyle from '../page.scss';
import Header from './component/Header';
require('./ServiceInfo.css');
class ServiceInfo extends Component {
    constructor( ...args ) {
        super( ...args);
    }
    componentDidMount() {
        document.title = '';
    }
    fnJump = (e) => {
        if (parseInt(e.target.alt)) {
            return;
        } else {
            console.log('');
        }
    }
    render() {
        const imgSrc = ["/static/union_store/yizhifu.png", "/static/union_store/quzhifu.png"]
        let aText = {
            "address": "",
            "username": "",
            "tel": "15263487856",
            "date": "2018-5-24 1520",
            "content": "",
            "price": "260",
            "status": "",
            "statusNum": 0                  //1  0
        };
        return (
            <div className={pageStyle['page']} id="serviceinfo">
                <React.Fragment>
                    <div className={'box'}>
                        <Header title="" />
                        <div className={'list'}>
                            <ul>
                                <li className={'item1'}>{aText.address}</li>
                                <li className={'item2'}>{aText.username}</li>
                                <li className={'item3'}>{aText.tel}</li>
                                <li className={'item4'}>{aText.date}</li>
                                <li className={'item5'}>{aText.content}</li>
                                <li className={'item6'}>{aText.price}</li>
                                <li className={'item7'}>{aText.status}</li>
                            </ul>
                            <div className={'img'}>
                                <img src={aText.statusNum ? imgSrc[0] : imgSrc[1]} alt={aText.statusNum} onClick={ this.fnJump } />
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}
export default ServiceInfo;
