import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './lntelligentLock.scss';
import pageStyle from '../page.scss';
import InputBox from '../../component/common/InputBox';
import ApiHelper from "../../store/ApiHelper";
import { lntelligent_lock } from '../../constants/ApiTypes';
import { Toast } from 'antd-mobile';

class LntelligentLock extends Component {
    static propTypes = {
        history: PropTypes.object
    };

    async componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
        
    }
    saveAccount = async () => {
        console.log(this.userName.state.value);
        console.log(this.userPhone.state.value);
        console.log(this.userAdress.state.value);
        
        const data = {
            userName: this.userName.state.value, 
            userPhone: this.userPhone.state.value,
            userAdress: this.userAdress.state.value
        };
        Toast.loading();
        const result = await ApiHelper.requestPost(lntelligent_lock, data);
        Toast.hide();
        if (result.code == 0) {
            console.log("success");
        } else {
            Toast.fail(result.msg, 1);
        }
    }

    render() {
        console.log("lntelligentLock");
        return (
            <div className={pageStyle['page']}>
                <React.Fragment>
                    <div>
                        <div>
                            <div className={style['button_installD']}>
                                <span></span>
                            </div>
                        </div>
                        <div className={pageStyle['line']}>
                        </div>
                        <div className={style['info']}>
                            <InputBox title="" plac="" ref={(ref) => this.userName = ref} />
                            <InputBox name="userPhone" title="" plac="" ref={(ref) => this.userPhone = ref} />
                            <InputBox name="userAdress" title="" plac="" ref={(ref) => this.userAdress = ref} />
                        </div>  
                        <div className={pageStyle['line']}>
                        </div>
                        <div className={style['botton_box']}>
                            <div className={style['botton_down']} onClick={this.saveAccount}>
                                <span></span>
                            </div>
                            <div className={style['botton_down']}>
                                <span></span>
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            </div>
        );
    }
}
export default LntelligentLock;