import React, { Component } from 'react';
import Header from './component/Header';
import WithDraw from './component/WithDraw';
import Search from '../../component/common/Search';
import pageStyle from '../page.scss';
import './ServiceFinance.css';
class ServiceFinance extends Component {
    constructor (...args) {
        super (...args);
        this.state = {
            cash: 'block',
            wrap: 'none',
            finance_type: 'type',
            withdraw: 'none'
        };
    }
    componentDidMount() {
        document.title = window.SITE_NAME + ' - ' + '';
    }
    fnWithCash = () => {
        this.setState({ withdraw: 'block' });
    }
    fnReset = () => {
        this.setState({ withdraw: 'none' });
    }
    fnSearch = () => {
        if ( this.state.cash === 'block' ) {
            this.setState({ cash: 'none', wrap: 'block', finance_type: 'active' });
        } else {
            this.setState({ cash: 'block', wrap: 'none', finance_type: 'type' });
        }

    }
    render () {
        let textList = [
            { type: "", time: "2018-03-08 15:25", info: "/15268975482", balance: "53777.03", trade: "+0.01" },
            { type: "", time: "2018-03-9 15:25", info: "/15268975482", balance: "53777.03", trade: "-0.01" },
            { type: "", time: "2018-03-08 15:25", info: "/15268975482", balance: "53777.03", trade: "+0.01" }
        ];
        let financeType = [
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' },
            { name: "", url: '#' }
        ];
        let listComp = (
            textList.map( ( item, index ) => {
                return (
                    <ul key={index} >
                        <li>{item.type}</li>
                        <li>{item.time}</li>
                        <li>{item.info}</li>
                        <strong className={item.trade > 0 ? "active" : ""}>{item.balance}</strong>
                        <span className={item.trade > 0 ? "active" : ""}>{item.trade}</span>
                    </ul>
                )
            })
        );
        let arr = [];
        let arr2 = [];
        financeType.forEach((item, index) => {
            arr.push(item);
            if ( index % 3 === 2 ) {
                arr2.push(arr);
                arr = [];
            } else if ( index === financeType.length - 1 ) {
                arr2.push(arr);
                arr = [];
            }
        });
        let typeComp = (
            arr2.map( ( row, index ) => {
                return (
                    <tr key={index}>
                        {
                            row.map((item, num) => {
                                return (
                                    <td key={num}><a href={item.url}>{item.name}</a></td>
                                )
                            })
                        }
                    </tr>
                )
            })
        );

        return (
            <div className={pageStyle['page']} id="servicefinance">
                <React.Fragment>
                    <div className={"box"}>
                        <WithDraw type={this.state.withdraw} aaa={this.fnReset} />
                        <Header title="" />
                        <div className={"cash"} style={{ display: this.state.cash }}>
                            <span>5897645.25</span>
                            <a href="javascript:;" onClick={ this.fnWithCash }></a>
                        </div>
                        <div className={"finance_type"}>
                            <a
                                href="javascript:;"
                                className={this.state.finance_type}
                                onClick={ this.fnSearch }
                            ></a>
                            <div className={"form"}>
                                <Search title=""/>
                            </div>
                        </div>
                        <div className={"list"}>
                            {listComp}
                        </div>
                        <div className="wrap" style={{ display: this.state.wrap }}>
                            <div className="content">
                                <table>
                                    <tbody>
                                        {typeComp}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            </div>
        )
    }
}
export default ServiceFinance
